<template>
	<div id="cancle-reason">
		<public-header title="取消订单理由"></public-header>
		<div class="content">
			<div class="padding-15-lr padding-10-tb font-14 c-gray">取消订单理由</div>
			<van-radio-group v-model="reasonSelected">
				<van-cell-group>
					<van-cell :title="item.text" clickable @click="onSelectReason(index)" class="no-fix" v-for="(item, index) in reasons" :key="index">
						<van-radio :name="index" />
					</van-cell>
					<!-- <van-cell title="其他理由" clickable @click="onSelectReason('other')" class="border-0px">
						<van-radio name="other" />
					</van-cell>
					<van-field
						v-model="reason_other"
						type="textarea"
						placeholder="请输入取消订单理由"
						rows="5"
						v-if="reasonSelected == 'other'"
					/> -->
					<van-field
						placeholder='填写备注（选填）'
						readonly
					/>
					<van-field
						v-model="remark"
						type="textarea"
						placeholder="取消订单备注"
						rows="5"
					/>
				</van-cell-group>
			</van-radio-group>

			<div class="padding-15">
				<van-button size="normal" block class="bg-primary font-16" @click='onSubmit'>提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				showPreLoading: true,
				reasons: {},
				reasonSelected: 0,
				remark: ''
			}
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'plateform/order/takeout/cancel_reason',
				}).then((res) => {
					let result = res.data.message;
					this.showPreLoading = false;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					this.reasons = result.message.reasons;
				})
			},
			onSelectReason(index) {
				this.reasonSelected = index;
			},
			onSubmit() {
				/*if(!this.reasonSelected) {
					this.$toast('请选择或输入取消订单理由');
					return false;
				}*/
				var reason = this.reasons[this.reasonSelected].type;
				var params = {
					url: 'plateform/order/takeout/cancel?id=' + this.order_id + '&type=cancel' + '&reason=' + reason + '&remark=' + this.remark,
					confirm: this.tip || '确定取消订单吗',
					successUrl: this.util.getUrl({path: 'pages/order/takeout', query: {status: this.order_status}})
				};
				this.util.jspost(params);
			}
		},
		mounted() {
			this.onLoad();
		},
		created(){
			this.query = this.$route.query;
			if(this.query) {
				this.order_id = this.query.order_id;
				this.tip = this.query.tip;
				this.order_status = this.query.order_status;
			}
		}
	}
</script>

<style>
	#cancle-reason .van-field{
		padding-top: 0px;
	}
	#cancle-reason .van-field textarea{
		background-color: #f5f5f5;
		padding: 10px;
	}
</style>


